<template>
  <div class="container">
    <!-- 引入头部组件 -->
    <navbar>
      <div class="back" slot="back"></div>
      <h4 slot="title" class="title">个人中心</h4>
    </navbar>
    <div class="user_info" @click="toedit">
      <div class="left">
        <img :src="$axios.defaults.baseURL + userinfo.head_img" alt />
      </div>
      <div class="right">
        <div class="title">
          <span class="gender"></span>
          <span>
            <i class="iconfont iconxingbienan" v-if="userinfo.gender == 1"></i>
            <i class="iconfont iconxingbienv" v-if="userinfo.gender == 0"></i>
            {{ userinfo.nickname }}
          </span>
          <p>{{ moment(userinfo.create_date).format("YYYY-MM-DD") }}</p>
        </div>
        <span class="iconfont iconjiantou1"></span>
      </div>
    </div>
    <!-- 封装listbar 组件 -->
    <div class="user_function">
      <!-- 传递自定义属性 -->
      <listbar
        v-for="(item, index) in listdata"
        :key="index"
        :label="item.label"
        :tips="item.tips"
        :link="item.link"
      ></listbar>
      <listbar :label="'退出'" :tips="''" @click.native="exit">
        <a href="#" slot="listbar">
          <div class="label">退出</div>
          <div class="tips">
            <span></span>
            <i class="iconfont iconjiantou1"></i>
          </div>
        </a>
      </listbar>
    </div>
    <!-- 底部标签栏 -->
    <toolbar>
      <div slot="myself">
        <a href="#">
          <span class="iconfont icon-wode" style="color:#ed4044"></span>
          <p style="color:#da4938">我的</p>
        </a>
      </div>
    </toolbar>
  </div>
</template>

<script>
// 导入listbar组件，@ 代表 是当前src文件夹
import listbar from "@/components/listbar.vue";
import navbar from "@/components/navbar.vue";
import toolbar from "@/components/toolbar.vue";
// 引入第三方的日期格式处理的工具库
import moment from "moment";
import { Dialog } from "vant";

export default {
  mounted() {
    const info = JSON.parse(localStorage.getItem("user_info"));
    // 等组件加载完毕后只需请求
    this.$axios({
      url: `/user/${info.user.id}`,
      method: "get",
      // 添加验证token
      headers: {
        Authorization: info.token
      }
    }).then(res => {
      const { data } = res.data;
      this.userinfo = data;
    });
  },
  methods: {
    toedit() {
      this.$router.push("/edit-profile");
    },
    exit() {
      Dialog.confirm({
        title: "标题",
        message: "确定要退出?"
      })
        .then(() => {
          // 清除token
          localStorage.clear();
          this.$router.push("/login");
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  data() {
    return {
      // 定义个人详情，初始值为对象
      userinfo: {},
      listdata: [
        {
          label: "我的关注",
          tips: "关注的用户",
          link: "/attention"
        },
        {
          label: "我的跟帖",
          tips: "跟帖回复",
          link: "/myfollow"
        },
        {
          label: "我的收藏",
          tips: "文章视频",
          link: "/mycollect"
        }
      ],
      moment
    };
  },
  // 注册组件
  components: {
    listbar,
    navbar,
    toolbar
  }
};
</script>

